




























import Vue from 'vue';
import { Rate, MAX_RATE } from '@models/Rate';
import StarIcon from '@components/StarIcon.vue';
import { getStrokeSvg } from '@helpers/stars';

export default Vue.extend({
  components: {
    StarIcon,
  },

  props: {
    maxValue: {
      type: Number,
      default: MAX_RATE,
    },

    initialValue: {
      type: Number,
      default: 0,
    },

    starSize: {
      type: Number,
      default: 30,
    },

    color: {
      type: String,
      default: 'ffcc00',
    },

    bgColor: {
      type: String,
      default: 'cccccc',
    },

    strokeColor: {
      type: String,
      default: '000000',
    },

    hasStroke: {
      type: Boolean,
      default: false,
    },

    isReadonly: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      rate: Rate.createNew(this.initialValue, this.maxValue),
    };
  },

  computed: {
    strokeSvg(): string {
      return getStrokeSvg(this.strokeColor);
    },
  },

  methods: {
    updateRateValue() {
      this.$emit('input', this.rate.get());
    },
  },
});
